import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { Col } from 'react-bootstrap';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
//import './Detail.scss'; 왜안됨?
import './Detail.css';




let 박스 = styled.div`
    padding: 20px;
    background: #efefef;
    border: 1px solid red;
`;
let 제목 = styled.h4`
    color: ${ props => props.색상 };
    font-size: 25px;
    background: ${ props => props.배경 };
`;



class Detail2 extends React.Component{ //옛날스타일Hook

    componentDidMount(){
        //Mount될때
    }

    componentWillUnmout(){
        //unMount될때
    }

}



function Detail(props){


    let [inputData, inputData변경] = useState();
    let [alert, alert변경] = useState(true);

    useEffect( ()=>{ //Detail컴포넌트 등장 & 업데이트될때 실행

        axios.get('https://codingapple1.github.io/shop/data2.json') //detail.js에 방문했을때 데이터를 갖고옴

        let 타이머 = setTimeout( ()=>{ alert변경(false) },2000 );
        console.log('dd');
        return ()=>{ clearTimeout(타이머) }  //setTimeout을 쓸때, clear해주기!!
    },[ alert, inputData ]); //[]를 통해, 적은 조건에만 실행하도록! 업데이트될때는 실행 안됨. 첫로드 될때만 실행됨

    useEffect( ()=>{
        //return function dd(){ Detail이 unMount될때 실행됨 }
        //useEffect는 여러개 사용 가능
    },[]); //[]안에 아무것도 안넣으면, 영영 실행 안됨. 


    let { id } = useParams();    
    let products = props.shoes.find(function(a){
        return a.id == id
    });

    let history = useHistory();

    return(
        <div className="container">
            <박스>
                <제목 색상={''} 배경="#aaa">재고가 101개 남았습니다.</제목>
            </박스>

            { inputData }
            <input onChange={ (e)=>{ inputData변경(e.target.value) } } />

            <div className="my-alert">
                이건뭐임1
            </div>

            {
                alert === true
                ? (<div className="my-alert-red">
                    이게바로 Alert창
                </div>)
                : null
            }

            <div className="row">
                <Col>
                    <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
                </Col>
                <Col>
                    <h4 className="pt-5">{ products.title }</h4>
                    <p>{ products.content }</p>
                    <p>{ products.price }</p>

                    <Info 재고 = {props.재고}></Info>

                    <button className="btn btn-danger" onClick={()=>{ props.재고변경([9,11,12]) }}>주문하기</button>
                    <button className="btn btn-warning" onClick={ ()=>{
                        history.goBack();
                    } }>뒤로가기</button>
                    <button className="btn btn-dark" onClick={ ()=>{
                        history.push('/qna')
                    } }>
                        문의하기
                    </button>
                </Col>
            </div>          
        </div>
    )
}


function Info(props){
    return (
        <p>재고 : {props.재고[0]}</p>
    )
}


export default Detail;