/* esLint-disable */
import { Navbar,Nav,NavDropdown,Jumbotron,Button,Col } from 'react-bootstrap';
import {useState} from 'react';
import './App.css';
import {name , name2} from './name.js';
import Data from './data.js';
import { Link, Route, Switch } from 'react-router-dom';
import Detail from './Detail.js';
import axios from 'axios';

function App() {

  let [shoes, shoes변경] = useState(Data);
  let [재고, 재고변경] = useState([10,11,12]);

  function list(){
    var array = [];
    for (var i = 0; i < 3; i++){  
      array.push('');
    }
    return array
  }

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Navbar.Brand href="#home">{name} {name2}</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link as={Link}> <Link to="/">Home</Link> </Nav.Link>
            <Nav.Link as={Link}> <Link to="/detail">Detail</Link> </Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>


      <Switch>
      
      <Route exact path="/">   
        <Jumbotron className="back">
          <h1>20% season OFF</h1>
          <p>
            This is a simple hero unit, a simple jumbotron-style component for calling
            extra attention to featured content or information.
          </p>
          <p>
            <Button variant="primary">Learn more</Button>
          </p>
        </Jumbotron>

        <h5>메인입니다</h5>

        <div className="container">
          <div className="row">
            { list() }
            {//내가만듬
              shoes.map(function(a,i){
                return(
                  <Col>
                    <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%"/>
                    <h4>{ a.title }</h4>
                    <p>{ a.content } &amp; { a.price }</p>
                  </Col>
                )
              })
            }
          </div>
          <div className="row">
            {//알랴줌
              shoes.map((a,i)=>{
                return <Card shoes={a} i={i} key={i} />
              })
            }
          </div>

          <button className="btn btn-primary" onClick={ ()=>{

            //axios.post('서버URL', {id : 'codingapple',pw:1234} ); //서버에 데이터를 보내고 싶을때

            //fetch.get('https://codingapple1.github.io/shop/data2.json') //fetch는 자바스크립트
            axios.get('https://codingapple1.github.io/shop/data2.json')
            .then( (result)=>{
              console.log(result.data);
              shoes변경([...shoes, ...result.data]);
            } )
            .catch( ()=>{
              console.log('실패했어요')
            } )

          } }>더보기</button>
        </div>

      </Route>


      <Route path="/detail/:id">
            <Detail shoes={shoes} 재고={재고} 재고변경={재고변경} />
      </Route>



      <Route path="/:id">
        <div>아무거나 적었을때 이거 보여주셈</div>
      </Route>


      </Switch>



    </div>
  );
}


function Card(props){
  return(
    <Col>
      <img src={ 'https://codingapple1.github.io/shop/shoes'+ (props.i+1) +'.jpg' } width="100%" />
      <h4>{ props.shoes.title }</h4>
      <p>{ props.shoes.content } &amp; { props.shoes.price }</p>
    </Col>
  )
}


export default App;
